import React from "react";
import styled from "styled-components";
import { Link } from "react-router-dom";

import background from "./images/background.svg";
import cloudLeft from "./images/cloud1.svg";
import cloudRight from "./images/cloud2.svg";

const BackgroundContainer = styled.div`
  width: 100%;
  height: 100%;
  background: url(${background}) no-repeat bottom, url(${cloudLeft}) no-repeat,
    url(${cloudRight}) no-repeat;
  background-size: auto, contain, contain;
  animation: 5s ease-in-out infinite alternate moveLeftCloud;
  @keyframes moveLeftCloud {
    from {
      background-position: bottom, top 20px left 0, top 20px right;
    }
    to {
      background-position: bottom, top 40px left 100px, top 20px right 50px;
    }
  }
`;

const LandingContainer = styled.div`
  font-size: 2.5em;
  height: 100%;
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 80%;
  margin: auto;
  @media screen and (min-width: 768px) {
    flex-direction: row;
    justify-content: center;
  }
  & a {
    text-decoration: none;
    color: inherit;
    display: inline-block;
    width: 100%;
  }

  & a:visited {
    color: inherit;
  }
`;
const LandingOption = styled.div`
  border: 2px solid;
  border-color: #6dd5ed #2193b0;
  width: 40%;
  height: auto;
  text-align: center;
  font-weight: bold;
  padding: 10px;
  margin-bottom: 20px;
  @media screen and (min-width: 768px) {
    width: 15%;
    border-width: 3px;
    margin-right: 5px;
  }
`;

const Landing = () => (
  <BackgroundContainer>
    <LandingContainer>
      <LandingOption>
        <Link to="/rent">RENT</Link>
      </LandingOption>
      <LandingOption>
        <Link to="/buy">BUY</Link>
      </LandingOption>
      <LandingOption>
        <Link to="/sell">SELL</Link>
      </LandingOption>
    </LandingContainer>
  </BackgroundContainer>
);

export default Landing;
